import React, { Component } from 'react';
import Layout from './hoc/Layout/Layout';
import BurgerBuilder from './containers/BurgerBuilder/BurgerBuilder';

class App extends Component {
	render() {
		return (
			<div>
				<Layout>
					<h1
						style={{
							textAlign: 'center',
							marginTop: '10px',
							fontFamily: 'Papyrus',
							fontSize: '40px',
							fontStyle: 'normal',
							fontVariant: 'normal',
							fontWeight: '700',
							lineHeight: '26px'
						}}
					>
						Happy Burger!!!<hr></hr>
					</h1>
					<BurgerBuilder />
				</Layout>
			</div>
		);
	}
}

export default App;



